import { NgClass, NgIf } from '@angular/common';
import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Optional,
  Output,
  Self,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { FormGroupDirective, FormsModule, NgControl, NgForm, ReactiveFormsModule, ValidatorFn } from '@angular/forms';
import { MatInput, MatInputModule } from '@angular/material/input';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS, MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatButtonModule } from '@angular/material/button';
import { A11yModule } from '@angular/cdk/a11y';

import { CURRENCY_MASK_CONFIG, CurrencyMaskConfig, CurrencyMaskModule } from 'ng2-currency-mask';

import { BaseComponent, maxValidator, minValidator } from 'sidsc-components/core';
import { DscTooltipModule } from 'sidsc-components/dsc-tooltip';

@Component({
  selector: 'dsc-input-currency',
  standalone: true,
  templateUrl: './dsc-input-currency.component.html',
  styleUrls: ['./dsc-input-currency.component.scss'],
  encapsulation: ViewEncapsulation.None,
  imports: [
    NgIf,
    NgClass,
    FormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatIconModule,
    MatTooltipModule,
    DscTooltipModule,
    MatButtonModule,
    ReactiveFormsModule,
    CurrencyMaskModule,
    A11yModule
  ],
  providers: [
    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: { appearance: 'outline' } }
  ]
})
export class DscInputCurrencyComponent extends BaseComponent<any> implements OnInit {
  @ViewChild('input', { static: true, read: MatInput })
  matInput!: MatInput;

  @Output() keydown: EventEmitter<any> = new EventEmitter<any>();

  @Input() prefix?: string;

  @Input() suffix?: string;

  @Input() iconPrefix?: string;

  @Input() iconSuffix?: string;

  @Input() precision: number = 2;

  @Input() customErrorMessages: { [key: string]: string } = {};

  @Input()
  get max(): number {
    return this._max;
  }
  set max(value: number | string) {
    const val = typeof value === 'string' ? parseFloat(value) : value;
    if (!Number.isNaN(val)) this._max = val;
  }

  private _max = NaN;

  isActive: boolean = false;

  @Input()
  get min(): number {
    return this._min;
  }
  set min(value: number | string) {
    const val = typeof value === 'string' ? parseFloat(value) : value;
    if (!Number.isNaN(val)) this._min = val;
  }

  private _min = NaN;

  get currencyOptions(): CurrencyMaskConfig {
    return {
      align: 'left',
      allowNegative: false,
      decimal: ',',
      precision: this.precision,
      prefix: this.prefix ?? 'R$ ',
      suffix: this.suffix ?? ' ',
      thousands: '.'
    }
  }

  constructor(@Optional() @Self() public ngControl: NgControl,
              @Optional() parentForm: NgForm,
              @Optional() parentFormGroup: FormGroupDirective
  ) {
    super(ngControl, parentForm, parentFormGroup);
    if (ngControl) ngControl.valueAccessor = this;
  }

  ngOnInit(): void {
    this._initValidators();
    this.updateOn = this.ngControl?.control?.updateOn;
  }

  private _initValidators(): void {
    if (this.ngControl) {
      const control = this.ngControl.control;
      control?.addValidators(this._getValidators());
      control?.updateValueAndValidity();
    }
  }

  private _getValidators(): ValidatorFn[] {
    return [maxValidator(this.max), minValidator(this.min)];
  }

  getErrorMessage(): string | null {
    if (!this.ngControl || !this.ngControl.errors) return null;

    const errors = this.ngControl.errors;
    const firstErrorKey = Object.keys(errors)[0];

    if (this.customErrorMessages && this.customErrorMessages[firstErrorKey]) {
      return this.customErrorMessages[firstErrorKey];
    }

    if (this.errorMessage) {
      return this.errorMessage;
    }

    switch (firstErrorKey) {
      case 'required':
        return 'Campo obrigatório.';
      case 'min':
        return this.min && !Number.isNaN(this.min)
          ? `O valor mínimo é R$ ${this.min.toLocaleString('pt-BR', { minimumFractionDigits: this.precision, maximumFractionDigits: this.precision })}.`
          : 'Valor abaixo do permitido.';
      case 'max':
        return this.max && !Number.isNaN(this.max)
          ? `O valor máximo é R$ ${this.max.toLocaleString('pt-BR', { minimumFractionDigits: this.precision, maximumFractionDigits: this.precision })}.`
          : 'Valor acima do permitido.';
      default:
        return 'Valor inválido.';
    }
  }

  get formFieldClasses(): string[] {
    return [
      this.ngControl && this.ngControl.value?.length
        ? 'mat-mdc-form-field--filled'
        : 'mat-mdc-form-field--empty',
      this.readOnly ? 'mat-mdc-form-field--readonly' : '',
      this.ngControl &&
      this.ngControl.invalid &&
      (this.ngControl.touched || this.ngControl.dirty) &&
      !this.readOnly
        ? 'error-field'
        : '',
      (this.isActive && !this.errorText) || (this.ngControl && this.ngControl.value?.length)
        ? 'active-field'
        : ''
    ].filter(Boolean);
  }

  onDrop(e: DragEvent){
    e.preventDefault();
    e.stopPropagation();
  }

  onDragOver(e: DragEvent){
    e.preventDefault;
  }

}
